import React, { Component } from 'react'
import $ from 'jquery/dist/jquery';
import 'popper.js';
import 'bootstrap/dist/js/bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap-icons/font/bootstrap-icons.css';
import 'bootstrap-icons/font/fonts/bootstrap-icons.woff';
import Topbar from '../topbar/topbar';
import firebase from '../../Firebase';

export class Advertisement extends Component {
    constructor(props) {
        super(props);
        this.state = {
            addingAdvertisement: false,
            freelancers: [],
            selectedFreelancer: "",
            categories: [],
            ads: []
        }
    }

    getFreelancers = () => {
        let freelancerRef = firebase.database().ref("freelancers");
        freelancerRef.once("value", snap => {
            let freelancerArray = []
            for (let i in snap.val()) {
                freelancerArray.push(snap.val()[i]);
            }
            this.setState({ freelancers: freelancerArray }, () => { this.getCategories() })
        }).catch(error => {
            console.log("Error while fetching freelancer details ", error);
        })
    }

    getCategories = () => {
        let categoriesRef = firebase.database().ref("categories");
        categoriesRef.once("value", snap => {
            let categoryArray = [];
            for (let i in snap.val()) {
                categoryArray.push(snap.val()[i]);
            }
            this.setState({ categories: categoryArray }, () => {
                this.getAds();
            });
        }).catch(error => {
            console.log("Error while fetching category details ", error);
        })
    }

    getAds = () => {
        let adRef = firebase.database().ref("advertisement");
        adRef.once("value", snap => {
            let adArray = [];
            for (let i in snap.val()) {
                adArray.push(snap.val()[i])
            }
            console.log("The ads array is ", adArray);
            this.setState({ ads: adArray });
        }).catch(error => {
            console.log("Error while fetching ads ", error);
        })
    }

    handleFreelancerChange = (e) => {
        this.setState({ selectedFreelancer: e.target.value })
    }

    createAd = () => {
        let file = document.getElementById("adImage").files
        this.setState({
            addingAdvertisement: true,
            file: file
        }, () => {
            let freelancer = this.state.freelancers.filter(f => f.id === this.state.selectedFreelancer);
            console.log("the freelancer details are ", freelancer);
            if (freelancer.length > 0) {
                freelancer = freelancer[0];
                let category = this.state.categories.filter(cat => {
                    let c = null
                    for (let i in cat.sub_categories) {
                        if (i === freelancer.sub_category) {
                            c = cat;
                            console.log("The category match ", i);
                            console.log("The category Freelancer category match ", freelancer.sub_category);
                        }
                    }
                    if (c !== null) {
                        return c
                    } else {
                        return false
                    }
                });
                console.log("The category is ", category);
                let file = this.state.file;
                if (file.length > 0) {
                    let name = `${freelancer.name}__${new Date()}`;
                    let adImageRef = firebase.storage().ref("Advertisement").child(name);
                    adImageRef.put(file[0]).then(() => {
                        adImageRef.getDownloadURL().then(url => {
                            console.log("the url for the ad image is ", url);
                            let adRef = firebase.database().ref("advertisement").push();
                            // these inside update 
                            adRef.update({
                                id: adRef.key,
                                category: { [category[0].id]: category[0] },
                                image: url,
                                freelancer: {
                                    [freelancer.id]: freelancer
                                },
                                filename: name,
                                uid: freelancer.id
                            }).then(() => {
                                console.log("Ad added successfully");
                                this.setState({
                                    addingAdvertisement: false,
                                    selectedFreelancer: "",
                                    file: []
                                }, () => {
                                    alert("Advertisement added succcessfully.");
                                    $("#exampleModal").modal('hide');
                                    this.getAds();
                                })
                            }).catch(error => {
                                console.log("error occured while adding the image url to database ", error);
                                this.setState({ addingAdvertisement: false }, () => {
                                    alert("Failed to add advertisement. Please try again later")
                                })
                            })
                        });
                    }).catch(error => {
                        console.log("The error occured while uploading the image to firebase storage ", error);
                        this.setState({ addingAdvertisement: false }, () => {
                            alert("Failed to add advertisement. Please try again later")
                        })
                    })
                } else {
                    console.log("the file.length is not > 0 ");
                    this.setState({ addingAdvertisement: false }, () => {
                        alert("Add an image for your advertisement")
                    })
                }
            } else {
                console.log("freelancer not selected")
                this.setState({ addingAdvertisement: false }, () => {
                    alert("Select a freelancer before adding advertisement")
                })
            }

        })
    }

    deleteAd = (ad) => {
        let imageRef = firebase.storage().ref("Advertisement").child(ad.filename);
        imageRef.delete().then(() => {
            let adRef = firebase.database().ref("advertisement").child(ad.id);
            adRef.remove().then(() => {
                alert('Ad deleted.');
                this.getAds();
            }).catch(error => {
                console.log("Failed to delete firebase ad data ", error);
                alert("Failed to delete Ad. Try again later");
            });
        }).catch(error => {
            console.log("Failed to delete Ad image ", error);
            alert("Failed to delete Ad. Try again later");
        })
    }

    componentDidMount() {
        this.getFreelancers();
    }


    render() {
        return (
            <div className="w-100 h-100">
                <Topbar logout={this.props.logout} />
                <div className="modal fade" id="exampleModal" tabIndex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
                    <div className="modal-dialog">
                        {this.state.addingAdvertisement ?
                            <div className="modal-content align-items-center justify-content-center" style={{ height: "10rem" }} >
                                <div className="spinner-border text-primary" role="status">
                                    <span className="sr-only">Uploading Image...</span>
                                </div>
                            </div>
                            :
                            <div className="modal-content">
                                <div className="modal-header">
                                    <h5 className="modal-title" id="exampleModalLabel">Add Advertisement</h5>
                                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                        <span aria-hidden="true">&times;</span>
                                    </button>
                                </div>
                                <div className="modal-body">
                                    <div className="mb-3">
                                        <label for="freelancer" className="form-label">Freelancer</label>
                                        <select name="freelancer" id="freelancer" className="custom-control custom-select" required onChange={this.handleFreelancerChange} >
                                            <option value="">Select Freelancer</option>
                                            {this.state.freelancers.map(freelancer => {
                                                return (
                                                    <option value={freelancer.id} className="text-capitalize">{freelancer.name}</option>
                                                )
                                            })}
                                        </select>
                                    </div>
                                    <div className="custom-file mb-3">
                                        <input type="file" className="custom-file-input" id="adImage" required />
                                        <label className="custom-file-label" for="adImage">Upload Ad Image</label>
                                    </div>
                                </div>
                                <div className="modal-footer">
                                    <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
                                    <button type="button" className="btn btn-primary" onClick={this.createAd} >Create Advertisement</button>
                                </div>
                            </div>
                        }
                    </div>
                </div>



                <div className="d-flex flex-column w-100 px-5 pt-5 pb-0" id="mainContainer">
                    <div className="d-flex flex-row justify-content-between align-items-center mb-3">
                        <h3 className="h3">Advertisements</h3>
                        <button className="btn btn-primary" data-toggle="modal" data-target="#exampleModal">Add Advertisement</button>
                    </div>
                    <div className="d-flex flex-row align-items-start flex-wrap" style={{ overflowY: "scroll" }}>
                        {
                            this.state.ads.map(ad => {
                                return (
                                    <div className="d-flex flex-column shadow-lg mr-3 mb-3" style={{ height: "20rem", width: "20rem", borderRadius: "1rem" }} key={ad.id}>
                                        <img src={ad.image} alt="Ad" style={{ width: "20rem", height: "15rem", borderTopLeftRadius: "1rem", borderTopRightRadius: "1rem" }} />
                                        <div className="d-flex flex-row justify-content-between align-items-center p-3 w-100">
                                            <h6 className="h6">{ad.freelancer[Object.keys(ad.freelancer)[0]].name}</h6>
                                            <button className="btn btn-primary" onClick={() => { this.deleteAd(ad) }}>
                                                <i class="bi bi-trash-fill"></i>
                                            </button>
                                        </div>
                                    </div>
                                )
                            })
                        }
                    </div>
                </div>
            </div>
        )
    }
}

export default Advertisement
