import React, { Component } from 'react';
import Fuse from 'fuse.js';
import 'jquery/dist/jquery';
import 'popper.js';
import 'bootstrap/dist/js/bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap-icons/font/bootstrap-icons.css';
import 'bootstrap-icons/font/fonts/bootstrap-icons.woff';
import './freelancers.css'
import Topbar from '../topbar/topbar';
import FreelancerCard from '../freelancerCard/freelancerCard';
import firebase from "../../Firebase";

export default class Freelancers extends Component {
    constructor(props) {
        super(props)
        this.state = {
            freelancers: [],
            userTag: "",
            searchText: "",
            searching: false,
            searchResult: []
        }
    }
    componentDidMount() {
        this.showFreelancerCards();
    }

    showFreelancerCards = async () => {
        console.log("fun call");
        Promise.all([await this.handleFreelancer()]).then((value) => {
            // console.log(value)
        }).catch(e => {
            // console.log(e.message)
        });
    }

    handleFreelancer = () => {
        try {
            return new Promise(resolve => {
                const ref = firebase.database().ref('/freelancers');
                ref.on('value', (snapshot) => {
                    let freelancers = []
                    snapshot.forEach(childSnapshot => {
                        var childKey = childSnapshot.key;
                        var childData = childSnapshot.val();
                        childData.key = childKey;
                        // console.log(childData);
                        freelancers.push(childData);
                        Promise.all([this.handletags(childData)]).then((value) => {
                            // console.log(value)
                        }
                        ).catch(e => {
                            // console.log(e.message)
                        });
                        this.setState({ freelancers: freelancers })
                    });
                    resolve(this.state.userTag);
                })
            })
        } catch (e) {
            // console.log(e.message)
        }
    }

    handletags = (data) => {
        // console.log("tag function");
        let tag = [];
        try {
            return new Promise(resolve => {
                if (data.tags !== undefined) {
                    data.tags.forEach((value, index) => {
                        firebase.database().ref('tags/' + value).once('value').then(snap => {
                            tag.push(snap.val());
                            this.setState({ userTag: tag });
                        })
                    })
                }
                resolve('solved');
            })
        } catch (e) {
            console.log(e.message)
        }
    }

    // showFreelancerCards = async() => {
    //     console.log("fun call");
    //     const ref = await firebase.database().ref('/freelancers');
    //     let list1 = []
    //     let tags = [];
    //     let tagData = null;
    //     await ref.once('value', (snapshot) => {
    //         snapshot.forEach( childSnapshot => {
    //             var childKey = childSnapshot.key;
    //             var childData = childSnapshot.val();
    //             // list.push(childData);
    //             let tagData = childData.tags;
    //             tagData.forEach(value =>{
    //                 firebase.database().ref('tags/'+ value).once('value').then(snap =>{
    //                     var data = snap.val();
    //                     tags.push(data.tag_name);
    //                     console.log("tags:",tags)
    //                 })
    //             })
    //             console.log("tag finish")
    //             childData.key = childKey;
    //             list1.push(childData);
    //         });
    //     })
    //     console.log(list1)
    //     this.setState({freelancers: list1})                                   
    // }


    tagHandle = (e) => {
        try {
            e.forEach(value => {
                firebase.database().ref('tags/' + value).once('value').then(snap => {
                    var data = snap.val();
                    return data.tag_name;
                })
            })
        } catch (error) {
            console.log(error.message);
        }
    }

    search = () => {
        this.setState({ searching: true }, () => {
            if (this.state.searchText === "" || this.state.searchText === null) {
                alert("Enter a freelancers name to search!")
            } else {
                let searchFuse = new Fuse(this.state.freelancers, {
                    includeScore: false,
                    keys: ['name']
                });
                const searchResult = searchFuse.search(this.state.searchText);
                let searchResultArray = [];
                searchResult.forEach(item=>{
                    searchResultArray.push(item.item)
                })
                console.log('the search Result is ', searchResult);
                this.setState({ searchResult: searchResultArray })
            }
        })
    }

    clearSearch = () => {
        this.setState({
            searchText: "",
            searching: false,
            searchResult: []
        })
    }

    render() {
        return (
            <div className="h-100">
                <Topbar logout={this.props.logout} />
                <div className="d-flex flex-column w-100 px-5 pt-5 pb-0" id="mainContainer">
                    <div className="d-flex flex-row justify-content-end">
                        <input type="text" name="username" id="username" className="form-control w-25 mx-3" placeholder="Search Freelancer" value={this.state.searchText} onChange={e => { this.setState({ searchText: e.target.value }) }} />
                        {
                            this.state.searching ?
                                <button className="btn btn-primary w-25 mx-3" onClick={this.clearSearch}><i className="bi bi-x-circle-fill mr-3"></i>Clear Search</button>
                                :
                                <button className="btn btn-primary w-25 mx-3" onClick={this.search}><i className="bi bi-search mr-3"></i>Search</button>
                        }
                        {/* <select name="category" id="category" className="custom-select w-25 mx-3" >
                            <option value="" selected>Select Category</option>
                        </select>
                        <select name="service" id="service" className="custom-select w-25 mx-3" >
                            <option value="" selected>Select Service</option>
                        </select>
                        <button className="btn btn-primary w-25 mx-3"><i className="bi bi-sliders mr-3"></i>Apply Filters</button> */}
                    </div>
                    {
                        this.state.searching ?
                            <>
                                <h4 className="h4 m-3 font-weight-light">Search Results</h4>
                                {/* <FreelancerCard name="Freelancer 1" /> */}
                                <div className="d-flex flex-row flex-wrap justify-content-around pb-5" id="freelancerCardContainer">
                                    {
                                        this.state.searchResult.map((val, index) => {
                                            return (
                                                <FreelancerCard name={val.name} title={val.title} freelancer={val} key={index} uid={val.key} bio={val.bio} update={this.showFreelancerCards} />
                                            )
                                        })
                                    }
                                </div>
                            </> :
                            <>
                                <h4 className="h4 m-3 font-weight-light">All Freelancers</h4>
                                {/* <FreelancerCard name="Freelancer 1" /> */}
                                <div className="d-flex flex-row flex-wrap justify-content-around pb-5" id="freelancerCardContainer">
                                    {
                                        this.state.freelancers.map((val, index) => {
                                            return (
                                                <FreelancerCard name={val.name} title={val.title} freelancer={val} key={index} uid={val.key} bio={val.bio} update={this.showFreelancerCards} />
                                            )
                                        })
                                    }
                                </div>
                            </>
                    }
                </div>
            </div>
        )
    }
}

