import React, { Component } from 'react'
import 'jquery/dist/jquery';
import 'popper.js';
import 'bootstrap/dist/js/bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap-icons/font/bootstrap-icons.css';
import 'bootstrap-icons/font/fonts/bootstrap-icons.woff';
import Topbar from '../topbar/topbar';
import firebase from '../../Firebase';

export default class Contest extends Component {
    constructor(props) {
        super(props);
        this.state = {
            contestName: "",
            contestDescription: "",
            googleFormLink: "",
            resultImage: "",
            showFileInput: false,
        }
    }

    handleContestName = (e) => {
        this.setState({ contestName: e.target.value });
    }

    handleContestDescription = (e) => {
        this.setState({ contestDescription: e.target.value })
    }

    handleGoogleFormLink = (e) => {
        this.setState({ googleFormLink: e.target.value })
    }

    getData = () => {
        let contestRef = firebase.database().ref("contest");
        contestRef.once("value").then(snap => {
            if (snap.val() !== null && snap.val() !== undefined && snap.val() !== {}) {
                console.log("The snap.val() is not empty", snap.val());
                this.setState({
                    contestName: snap.val().contestName,
                    contestDescription: snap.val().contestDescription,
                    googleFormLink: snap.val().googleFormLink,
                    resultImage: snap.val().resultImage
                })
            } else {
                console.log("the snap.val() is ", snap.val());
            }
        })
    }

    save = (e) => {
        e.preventDefault();
        let contestRef = firebase.database().ref("contest");
        contestRef.set({
            contestName: this.state.contestName,
            contestDescription: this.state.contestDescription,
            googleFormLink: this.state.googleFormLink,
            resultImage: this.state.resultImage
        }).then(() => {
            alert("Contest saved successfully");
        }).catch(error => {
            alert("Failed to save contest");
            console.log("the error is ", error);
        })
    }

    uploadResult = (e) => {
        e.preventDefault();
        let file = document.getElementById("resultImage").files;
        if (file.length > 0) {
            let imageRef = firebase.storage().ref("contestResult").child("result");
            imageRef.put(file[0]).then(() => {
                imageRef.getDownloadURL().then(url => {
                    console.log("the result image url is ", url);
                    let contestRef = firebase.database().ref("contest");
                    contestRef.update({ resultImage: url }).then(() => {
                        alert("Result uploaded successfully");
                    }).catch(error => {
                        console.log("error happened while updating database with url", error);
                        alert("Failed to upload result. Try again later");
                    })
                }).catch(error => {
                    console.log("Error occured while getting download url of uploaded image ", error);
                    alert("Failed to uplaod result. Try again later");
                })
            }).catch(error => {
                console.log("Error occured while uploading file to storage ", error);
                alert("Failed to upload result. Try again later");
            })
        } else {
            alert("Pick an image to upload");
        }
    }

    showResultUploadDialog = (e) => {
        e.preventDefault();
        this.setState({ showFileInput: true })
    }

    changeImage = (e) => {
        if (e.target.files && e.target.files[0]) {
            let image = e.target.files[0];
            let imageElement = document.getElementById("result");
            imageElement.src = URL.createObjectURL(image);
            imageElement.onload = function () {
                URL.revokeObjectURL(imageElement.src)
            }
        }
    }

    deleteResult = (e) => {
        e.preventDefault();
        let ref = firebase.storage().ref("contestResult").child("result");
        ref.delete().then(() => {
            let contestRef = firebase.database().ref("contest");
            contestRef.update({ resultImage: "" }).then(() => {
                alert("Result deleted successfully");
                this.getData();
            }).catch(error => {
                console.log("Error occured while deleting the result, UPDATING DB ", error);
                alert("Failed to delete result")
            })
        }).catch(error => {
            console.log("Error while deleting the image ", error);
            alert("Failed to delete result")
        })

    }

    componentDidMount() {
        this.getData();
    }

    componentWillUnmount() {
    }


    render() {
        return (
            <div className="w-100 h-100">
                <Topbar logout={this.props.logout} />
                <div className="d-flex flex-row w-100 px-5 pt-5 pb-0" id="mainContainer">
                    <div className="col" style={{ overflowY: "scroll" }}>
                        <h3 className="h3 text-center mb-5">Contest Details</h3>
                        <form>
                            <div className="form-group">
                                <label htmlFor="contestName">Name of contest</label>
                                <input type="text" name="contestName" id="contestName" placeholder="Contest Name" value={this.state.contestName} onChange={this.handleContestName} className="form-control" required />
                            </div>
                            <div className="form-group">
                                <label htmlFor="contestDescription">Description</label>
                                <textarea type="text" name="contestDescription" rows={5} id="contestDescription" placeholder="Contest Description" value={this.state.contestDescription} onChange={this.handleContestDescription} className="form-control" required />
                            </div>
                            <div className="form-group">
                                <label htmlFor="googleForm">Google Form link</label>
                                <input type="url" name="googleForm" pattern="https://.*" id="googleForm" placeholder="https://exampleform.google.com" value={this.state.googleFormLink} onChange={this.handleGoogleFormLink} className="form-control" required />
                            </div>
                            {
                                this.state.resultImage !== "" && this.state.resultImage !== null && this.state.resultImage !== undefined ?
                                    <div className="form-group d-flex flex-column">
                                        <label htmlFor="uploadedResult">Result</label>
                                        <img src={this.state.resultImage} alt="Uploaded Result" style={{ width: "15rem", height: "15rem", objectFit: "scale-down" }} />
                                    </div>
                                    : <></>
                            }
                            <div className="d-flex flex-row justify-content-around align-items-center">
                                <button className="btn btn-primary" onClick={this.save}>Save</button>
                                {
                                    this.state.resultImage !== "" && this.state.resultImage !== null && this.state.resultImage !== undefined ?
                                        <button className="btn btn-primary" onClick={this.deleteResult}>Delete Result</button>
                                        : <></>
                                }
                                {!this.state.showFileInput ?
                                    <button className="btn btn-primary" onClick={this.showResultUploadDialog} >Upload Result Image</button>
                                    : <></>
                                }
                            </div>
                            {
                                this.state.showFileInput ?
                                    <div className="d-flex flex-row justify-content-around align-items-center my-5">
                                        <input type="file" name="resultImage" id="resultImage" placeholder="Upload Image" accept="image/*" onChange={this.changeImage} />
                                        <button className="btn btn-primary" onClick={this.uploadResult}>Upload</button>
                                        <img src="" name="result" id="result" alt="result" style={{ width: "30%", objectFit: "scale-down" }} />
                                    </div>
                                    :
                                    <>
                                    </>
                            }
                        </form>
                    </div>
                    <div className="col">
                        <div className="d-flex flex-column h-100 w-100" style={{ overflowY: "scroll" }}>
                            <h3 className="h3 text-center mb-3">{this.state.contestName}</h3>
                            <p className="p text-justify">{this.state.contestDescription}</p>
                            <div className="d-flex flex-column h-100">
                                <iframe src={this.state.googleFormLink} frameBorder="0" className="h-100 w-100" title="form" ></iframe>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}